<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" v-bind:src="seller.avatar">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}} / {{seller.deliveryTime}}分钟送达
        </div>
        <div class="supports" v-if="seller.supports">
          <span class="icon" v-bind:class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div class="support-count"  v-on:click="showDetail">
        <span class="count">5个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div class="bulletin-wrapper">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img width="100%" height="100%" :src="seller.avatar">
    </div>
    <div class="detail" v-show="detailShow" transition="fade">
      <div class="detail-wrapper clearfix">
        <div class="detail-main">
          <h1 class="name">{{seller.name}}</h1>
          <div class="star-wrapper">
            <star :size="48" :score="seller.score"></star>
          </div>
          <div class="title">
            <div class="line"></div>
            <div class="text">优惠信息</div>
            <div class="line"></div>
          </div>
          <ul class="supports">
            <li class="support-item" v-for="item in seller.supports">
              <span class="icon" :class="classMap[seller.supports[$index].type]">1</span>
              <span class="description">{{seller.supports[$index].description}}</span>
            </li>
          </ul>
          <div class="title">
            <div class="line"></div>
            <div class="text">商家公告</div>
            <div class="line"></div>
          </div>
          <div class="bulletin">
            <p class="text">{{seller.bulletin}}</p>
          </div>
        </div>
      </div>
      <div class="detail-close">
        <i class="icon-close" v-on:click="closeDetail"></i>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import star from 'components/star/star';

  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data() {
      return {
        detailShow: false
      };
    },
    methods: {
      showDetail() {
        this.detailShow = true;
      },
      closeDetail() {
        this.detailShow = false;
      }
    },
    created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    components: {
      star
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '../../common/stylus/mixin'

  .header
    position: relative
    overflow: hidden
    background-color: rgba(7, 17, 27, 0.5)
    .content-wrapper
      position: relative
      padding: 24px 12px 18px 24px
      font-size: 0
      .avatar
        display: inline-block
        vertical-align: top;
      .content
        display: inline-block
        margin-left: 16px
        .title
          margin: 2px 0 8px 0
          .brand
            display: inline-block
            vertical-align: top
            width: 30px
            height: 18px
            bg-image('brand')
            background-size: 30px 18px
            background-repeat: no-repeat
          .name
            margin-left: 6px
            color: rgb(255, 255, 255)
            line-height: 18px
            font-weight: bold
            font-size: 16px
        .description
          font-size: 12px
          font-weight: 200
          color: rgb(255, 255, 255)
          line-height: 12px
          margin-bottom: 10px
        .supports
          .icon
            vertical-align: top
            display: inline-block
            width: 12px
            height: 12px
            background-size: 12px 12px
            background-repeat: no-repeat
            &.decrease
              bg-image('decrease_1')
            &.discount
              bg-image('discount_1')
            &.guarantee
              bg-image('guarantee_1')
            &.invoice
              bg-image('invoice_1')
            &.special
              bg-image('special_1')
          .text
            margin-left: 4px
            font-size: 10px
            font-weight: 200
            color: rgb(255, 255, 255)
            line-height: 12px
      .support-count
        position: absolute
        right: 12px
        bottom: 14px
        padding: 0px 8px
        background-color: rgba(0, 0, 0, 0.2)
        line-height: 24px
        border-radius: 14px
        font-size: 10px
        font-weight: 200
        .count
          color: rgb(255, 255, 255)
        .icon-keyboard_arrow_right
          color: rgb(255, 255, 255)
    .bulletin-wrapper
      position: relative
      line-height: 28px
      padding: 0 22px 0 12px
      white-space: nowrap
      overflow: hidden
      text-overflow: ellipsis
      color: #fff
      background: rgba(7, 17, 27, 0.2)
      .bulletin-title
        display: inline-block
        vertical-align: middle;
        width: 22px
        height: 12px
        background-size: 22px 12px
        background-repeat: no-repeat
        bg-image('bulletin')
      .bulletin-text
        margin: 0 4px
        font-size: 10px
        color: rgb(255, 255, 255)
      .icon-keyboard_arrow_right
        position: absolute
        right: 6px
        bottom: 0
        line-height: 28px
    .background
      position: absolute
      top: 0
      z-index: -1
      filter: blur(10px)
      width: 100%
      height: 100%
    .detail
      position: fixed
      top: 0
      left: 0
      z-index: 100
      width: 100%
      height: 100%
      overflow: auto
      backdrop-filter: blur(10px)
      transition: all 0.5s
      &.fade-transition
        opacity: 1
        background-color: rgba(7,17,27,0.8)
      &.fade-enter, &.fade-leave
        opacity: 0
        background-color: rgba(7,17,27,0)
      .detail-wrapper
        width: 100%
        min-height: 100%
        .detail-main  
          margin-top: 64px
          padding-bottom: 64px
          color: rgb(255, 255, 255)
          .name
            font-size: 16px
            font-weight: 700
            line-height: 16px
            color: rgb(255, 255, 255)
            text-align: center
          .star-wrapper
            margin-top: 16px
            text-align: center
            padding: 2px 0
          .title
            display: flex
            width: 80%
            margin: 28px auto 24px auto
            .line
              flex: 1
              position: relative
              top: 6px
              border-top: 1px solid rgba(255,255,255,0.2)
            .text
              padding: 0 12px
              font-weight: 700
              font-size: 14px
          .supports
            width: 80%
            margin: 0 auto
            .support-item
              margin-bottom: 12px
              padding: 0 12px
              &:last-child
                margin-bottom: 0
              .icon
                display: inline-block
                width: 16px
                height: 16px
                vertical-align: middle
                margin-right: 6px
                background-size: 16px 16px
                background-repeat: no-repeat
                &.decrease
                  bg-image('decrease_2')
                &.discount
                  bg-image('discount_2')
                &.guarantee
                  bg-image('guarantee_2')
                &.invoice
                  bg-image('invoice_2')
                &.special
                  bg-image('special_2')
              .description
                font-weight: 200
                font-size: 12px 
                line-height: 16px 
                color: rgb(255,255,255)
          .bulletin
            width: 80%
            margin: 0 auto
            .text
              font-size: 12px 
              font-weight: 200px
              line-height: 24px
              color: rgb(255,255,255)
              padding: 0 12px
      .detail-close
          position: relative
          width: 32px
          height: 32px
          margin: -64px auto 0 auto
          clear: both
          font-size: 32px
          color: rgba(255,255,255,0.5)
</style>


